* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.page {
  background: #ffb6c1;
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.circles {
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
}

.circles__circle {
  background: #fff;
  border-radius: 50%;
  width: 15px;
  height: 15px;
  animation: 2s linear infinite move_circle;
  position: absolute;
}

.circles__shadow {
  justify-content: center;
  align-items: center;
  width: 400px;
  height: 180px;
  display: flex;
  position: absolute;
}

.circles__shadow:after {
  content: "";
  opacity: .4;
  background: #000;
  border-radius: 50%;
  width: 20px;
  height: 5px;
  animation: 2s linear infinite move_shadow;
  position: absolute;
  bottom: 0;
}

.circles__shadow:nth-child(2):after {
  animation-delay: -.3s;
}

.circles__shadow:nth-child(3):after {
  animation-delay: -.6s;
}

.circles__shadow:nth-child(2) .circles__circle {
  animation-delay: -.3s;
}

.circles__shadow:nth-child(3) .circles__circle {
  animation-delay: -.6s;
}

@keyframes move_circle {
  0% {
    transform-origin: 400%;
    transform: rotate(0);
  }

  50% {
    transform-origin: 400%;
    transform: rotate(360deg);
  }

  50.1% {
    transform-origin: -300%;
    transform: rotate(0);
  }

  100% {
    transform-origin: -300%;
    transform: rotate(-360deg);
  }
}

@keyframes move_shadow {
  12.5% {
    transform: translate(50px)scale(.6);
  }

  25% {
    transform: translate(110px);
  }

  37.5% {
    transform: translate(50px)scale(1.4);
  }

  50% {
    transform: translate(0);
  }

  62.5% {
    transform: translate(-50px)scale(.6);
  }

  75% {
    transform: translate(-110px);
  }

  87.5% {
    transform: translate(-50px)scale(1.4);
  }

  100% {
    transform: translate(0);
  }
}
